.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-button {
  width:50px;
  height:50px;
}


.TabView-Tab-Bar {
  background-color: lightblue;
  height: 50px;
}

.TabView-Tab {
  background-color: lightblue;
  border-radius: 0;
  height: 50px;
}

.TabView-Tab-Selected {
  background-color: cyan;
  border-radius: 0;
  height: 50px;
}

.TabView-Content {
  margin-left: 10px;
}